export const getShortAddress = (longAddress) => {
  if (!longAddress) {
    return "";
  }
  const shortUserAddress =
    longAddress.substring(0, 7) +
    "...." +
    longAddress.substring(longAddress.length - 7, longAddress.length);

  return shortUserAddress;
};

